import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Header from '../components/Header';
import Footer from '../components/Footer';
import './Home.css';
import { useSolWeb3 } from '../hooks/solweb3';
import { useSendMsg } from '../hooks/useSendMsg'
import axios from 'axios';
import {shortAddress} from '../hooks/address'
import { useRandom } from '../hooks/random';

function Home() {
  const [message, setMessage] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    setIsLoading(false);
  }, []);

  
  // newaddr history random
  const [switchType, _switchType] = useState('random')
  const [newToAddr, _newToAddr] = useState('')

  const [sending, _sending] = useState(false)

  const { address, provider, onConnect } = useSolWeb3()
  const { onSend } = useSendMsg()

  const [friends, _friends] = useState([])
  const { random_list } = useRandom()

  console.log('random_list----', random_list)
  useEffect(() => {
    if(!address){
      return
    }
    if(switchType !== 'history'){
      return
    }
    axios.get('/api/friend/list', {
      params: {
        address,
      }
    }).then((res) => {
      const data = res.data.data
      console.log('friends data', data)
      if(data){
        const list = JSON.parse(data[0].friends)
        console.log('friends list', list)
        if(list){
          _friends(list)
        }
      }
    })
  }, [address, switchType])

  const [msglist, _msglist] = useState([])
  const [selectAddr, _selectAddr] = useState('')
  const onSelectAddress = (addr, type) => {
    console.log('addr::', addr)
    _selectAddr(addr)
    if(type === 'random'){
      setMessage('👋')
      return
    }
    setMessage('')

    axios.post('/api/msg/ab/addr', {
      a: address,
      b: addr,
      limit: 3000,
    }).then((res) => {
      const total = res.data.total
      const data = res.data.data.reverse()
      console.log('msg list', data, total)
      _msglist(data)

      try {
        setTimeout(() => {
          document.getElementsByClassName(data[data.length - 1].hash)[0].scrollIntoView()
        }, 300)
      } catch (error) {
        //
      }
    })
  }

  console.log('home address::', address)

  const [send_success, _send_success] = useState(false)
  return (
    <>
      <div className="loading" style={{ display: isLoading ? 'block' : 'none' }}>
        <span className="loadingfa xuanzhuan">
          <img src="images/loading.png" alt="" />
        </span>
      </div>
      <Header />
      <div className="index">
        <div className="section1">
          <div className="leftChoose">
            <a href="#" onClick={() => _switchType('random')} className={switchType === 'random' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon3.png" alt="" />
              </span>
              <span className="title">Random on chain chat</span>
            </a>
            <a href="#" onClick={() => _switchType('newaddr')} className={switchType === 'newaddr' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon1.png" alt="" />
              </span>
              <span className="title">Send message to specified address</span>
            </a>
            <a href="#" onClick={() => _switchType('history')} className={switchType === 'history' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon2.png" alt="" />
              </span>
              <span className="title">History List</span>
            </a>
            <a href="#">
              <span className="icon">
                <img src="images/icon4.png" alt="" />
              </span>
              <span className="title">ShadowLine(Coming soon)</span>
            </a>
            <a href="#">
              <span className="icon">
                <img src="images/icon5.png" alt="" />
              </span>
              <span className="title">On chain MEME(coming soon)</span>
            </a>
            <a href="#">
              <span className="icon">
                <img src="images/icon6.png" alt="" />
              </span>
              <span className="title">Address analysis(coming soon)</span>
            </a>
          </div>
          {switchType === 'newaddr' ? <></> : <div className="centerList">
            {switchType === 'random' ? <div className="title">
              <div className="tit">address</div>
              <button className="changebtn" type="button">
                auto-refresh
              </button>
            </div> : <></>}
            <div className="list">
              {
              switchType === 'history' ? <>
                {friends?.map((e) => {
                return <a className={selectAddr === e ? "current" : ''} href="#" onClick={() => onSelectAddress(e)} key={e}>{shortAddress(e)}</a>
              })}
              </> :
              switchType === 'random' ? <>
                {random_list && random_list?.map((e) => {
                return <a className={selectAddr === e ? "current" : ''} href="#" onClick={() => onSelectAddress(e, 'random')} key={e}>{shortAddress(e)}</a>
              })}
              </> : <>
              </>}
            </div>
          </div>}
          <div className="clear wap" />
          <div className="rightContent" style={switchType === 'newaddr' ? { width: '92%' } : {} }>
            {false && <div className="link">
              <div className="link1">
                <a href="#">
                  <img src="images/li1.png" alt="" />
                </a>
                <a href="#">
                  <img src="images/li2.png" alt="" />
                </a>
              </div>
              <div className="link2">
                <a href="#">
                  <img src="images/ic1.png" alt="" />
                </a>
                <a href="#">
                  <img src="images/ic2.png" alt="" />
                </a>
                <a href="#">
                  <img src="images/ic3.png" alt="" />
                </a>
              </div>
            </div>}
            {switchType === 'newaddr' ? <>
              <div style={{ marginTop: '20px' }}></div>
              <input style={{ background: 'transparent', padding: '10px', width: '100%', border: '2px solid #81fa27', color: '#81fa27', borderRadius: '6px' }} 
              placeholder='Send message to specified address' value={newToAddr} onInput={(e) => {
                  _newToAddr(e.target.value)
                }} />
                <div style={{ marginBottom: '30px', }}></div>
              </> : <></>}
            {switchType === 'history' && <div style={{ flex: 1, overflow: 'auto', scrollBehavior: 'smooth', padding: '20px 0px' }}>
              {msglist.map((e) => {
                const isMe =  e.form_addr === address
                return <div className={['msg_hash_item', e.hash].join(' ')} key={e.hash} style={{ textAlign: e.form_addr === address ? 'right' : 'left', fontSize: '16px' }}>
                  {/* <div>Time: {Date(e.add_time).toLocaleString()}</div> */}
                  {/* <div>{e.form_addr}</div> */}
                  {isMe ? <>
                    <span>{e.text_content}</span>
                    <span className='msg_send_name'>:{shortAddress(e.form_addr)}</span>
                  </> : <>
                    <span className='msg_send_name'>{shortAddress(address)}:</span>
                    <span>{e.text_content}</span>
                  </>}
                </div>
              })}
            </div>}
            <div className="textContent" style={switchType === 'history' ? { height: '60px' } : {}}>
              <textarea
                name=""
                id=""
                placeholder="Nice to meet you at NeoCircle!"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                style={switchType === 'history' ? { height: 'auto', padding: '10px 20px' } : {}}
              />
              <div className="total">
                <span>{message.length}</span>/600
              </div>
            </div>
            {/* <button className="send" type="button" onClick={() => navigate('/chat')}> */}
            <button disabled={sending} className="send" type="button" onClick={async () => {
              if(message.length > 600){
                return
              }
              // onSend({ toAddress: 'DdZ1Yh6ShRLGmj7gJFLRLPPmYgn8qcx7htsG8HaXN381', msg: 'hi'})
              // onSend({ toAddress: 'EtNYDEQWztPxWJ1noZRVELJXGr27tMFL7faySRo9vsHk', msg: '??'})
              // onSend({ toAddress: 'BQoZwUqfFusUytq6SeJV1cXZ8PYtu5jtdVxAszKnwDSx', msg: message})
              if(switchType === 'newaddr') {
                console.log('newToAddr::', newToAddr)
                _sending(true)
                const tx = await onSend({ toAddress: newToAddr, msg: message})
              }
              if(switchType === 'history') {
                console.log('history::', selectAddr)
                _sending(true)
                const tx = await onSend({ toAddress: selectAddr, msg: message})
                _msglist((m) => {
                  return [...m, {
                    form_addr: address,
                    // hash: 'mock-' + new Date(),
                    hash: tx,
                    to_addr: selectAddr,
                    msgtype: 'text',
                    text_content: message,
                    addTime: +new Date(),
                  }]
                })
                setTimeout(() => {
                  document.getElementsByClassName(tx)[0].scrollIntoView()
                }, 300)
              }
              
              if(switchType === 'random') {
                console.log('random::', selectAddr)
                _sending(true)
                const tx = await onSend({ toAddress: selectAddr, msg: message})
                _send_success(true)
                setTimeout(() => {
                  _send_success(false)
                }, 3000)
              }
              _sending(false)
              setMessage('')
            }}>
              Send
            </button>
          </div>
        </div>
      </div>
      <Footer />
      {send_success && <div style={{ position: 'fixed', zIndex: '9999', top: '30px', left: '50%', marginLeft: '-90px', padding: '10px 20px', color: '#81FA27', border: '1px solid ', fontSize: '16px'}}>
        Message sent successfully  
      </div>}
    </>
  );
}

export default Home; 