import React, { useEffect, useState } from 'react';
import { useSolWeb3 } from '../hooks/solweb3';
import { shortAddress } from '../hooks/address'

function Header() {
  const [showAlert, setShowAlert] = useState(false);

  const handleConnectClick = (e) => {
    console.log(showAlert);
    
    e.preventDefault();
    setShowAlert(!showAlert);
  };

  const { address, provider, onConnect } = useSolWeb3()

  useEffect(() => {
    if(localStorage.wallet_type === 'phantom'){
      console.log('auto connect phantom')
      onConnect('phantom')
    }
    if(localStorage.wallet_type === 'okxwallet'){
      console.log('auto connect okxwallet')
      onConnect('okxwallet')
    }
  }, [])
  return (
    <div className="head">
      <div className="logo">
        <a href="#">
          <img src="images/logo.png" alt="" />
        </a>
      </div>
      <div className="rightTxtLink">
        {/* <input
          type="text"
          placeholder="Number of tokens held: 0"
          className="words"
        /> */}
        {/* <div className="lan">
            <h2 className="h2tit">
                <a href="javascript:;">
                    <img src="images/in1.png" alt="" />
                    <span>EN</span>
                </a>
            </h2>
            <div className="slide">
                <a href="javascript:;">
                    <img src="images/in2.png" alt="" />
                    <span>CN</span>
                </a>
            </div>
        </div> */}
        {address ? <a className='wallet-address'>{shortAddress(address)}</a> : <a href="#" className="connect" onClick={handleConnectClick}>
          Connect Wallet
        </a>}
      </div>
      <div 
        className="alertModel" 
        id="alertModel1" 
        style={{ display: showAlert ? 'block' : 'none' }}
      >
        <div className="shadow" onClick={handleConnectClick} />
        <div className="center">
          <div className="title">Connect wallet</div>
          <div className="list">
            <a onClick={() => {
              onConnect('phantom')
              setShowAlert(false)
            }}>
              <img src="images/icv1.png" alt="" />
              <span>Phantom</span>
            </a>
            <a onClick={() => {
              onConnect('okxwallet')
              setShowAlert(false)
            }}>
              <img src="images/okx.png" alt="" />
              <span>okxwallet</span>
            </a>
            {/* <a href="#">
              <img src="images/icv2.png" alt="" />
              <span>Solflare</span>
            </a>
            <a href="#">
              <img src="images/icv3.png" alt="" />
              <span>Token Pocket</span>
            </a>
            <a href="#">
              <img src="images/icv4.png" alt="" />
              <span>TrustWallet</span>
            </a> */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header; 