import { useEffect, useState } from "react";
import {
    clusterApiUrl,
    Connection,
    PublicKey,
    SystemProgram,
    Transaction,
    TransactionInstruction,
  } from '@solana/web3.js';

export const useRandom = () => {
    
    const connection = new Connection(clusterApiUrl('devnet'), 'confirmed');
    // const [addresses, _addresses] = useState([])
    const [random_address, _random_address] = useState('')
    const [random_list, _random_list] = useState('')
    useEffect(() => {
        if(!connection){
            return
        }
        const run = async () => {

            try {
                const connectHeight = await connection.getBlockHeight()
                console.log('connectHeight::', connectHeight)
                const new_block = await connection.getBlock(connectHeight, {
                    // encoding: 'json',
                    // transactionDetails: 'signatures'
                })
                console.log('new_block::', new_block)
                const list = new_block.transactions.map(e => e.transaction.message.accountKeys[0].toBase58())//.flatMap()
                console.log('----address-----list', list)
                // _addresses(list)
                localStorage.new_address = JSON.stringify(list)
                const ra = list[Math.random() * list.length | 0]
                _random_list([...new Set(list)])
                console.log('ra:::::', ra)
                _random_address(ra)
            } catch (error) {
                const list = JSON.parse(localStorage.new_address)
                const ra = list[Math.random() * list.length | 0]
                _random_list([...new Set(list)])
                console.log('ra:::::', ra)
                _random_address(ra)
            }

        }
        run()
    }, [])
    return {
        random_address,
        random_list,
    }
}