import React, { useState, createContext, useContext } from 'react';

const getProvider = (type) => {
    if(type === 'phantom'){
        if ('phantom' in window) {
            const provider = window.phantom?.solana;
    
            if (provider?.isPhantom) {
                localStorage.wallet_type = 'phantom'
                return provider;
            }
            window.open('https://phantom.app/', '_blank');
        }
    }
    if(type === 'okxwallet'){
        if ('okxwallet' in window) {
            const provider = window.okxwallet?.solana;
            localStorage.wallet_type = 'okxwallet'
            return provider;
        }
    }
};

export const SolWeb3Context = createContext();
export const Web3Provider = ({ children }) => {
    const [address, _address] = useState('')
    const [provider, _provider] = useState()
    return <SolWeb3Context.Provider value={{address, _address, provider, _provider}}>
        {children}
    </SolWeb3Context.Provider>
}
export const useSolWeb3 = () => {
    const { address, _address, provider, _provider } = useContext(SolWeb3Context);
    const onConnect = async (type) => {
        console.log('onConnect:', type)
        const provider = getProvider(type); // see "Detecting the Provider"
        try {
            const resp = await provider.connect();
            console.log(resp.publicKey.toString());
            _provider(provider)
            _address(resp.publicKey.toString())
        } catch (err) {
            // { code: 4001, message: 'User rejected the request.' }
        }
    }
    return {
        onConnect,
        provider,
        address,
    }
}