import { useCallback, useEffect, useState } from 'react'
import { useSolWeb3 } from "./solweb3";
import { transferNativeSol } from './send'
import AES from 'crypto-js/aes'
import axios from 'axios';
import {
    clusterApiUrl,
    Connection,
    PublicKey,
    SystemProgram,
    Transaction,
    TransactionInstruction,
    // SPL_ASSOCIATED_TOKEN_ACCOUNT_PROGRAM_ID,
    // TOKEN_PROGRAM_ID,
  } from '@solana/web3.js';

export const useSendMsg = () => {
    const { address, provider, onConnect } = useSolWeb3()
    const connection = new Connection(clusterApiUrl('devnet'), 'confirmed');
    
    const onSend = useCallback(async ({toAddress, msg}) => {
        if(!address) {
          return 
        }
        console.log('address---:', address)
        const payerPublicKey = new PublicKey(address)
        console.log('toAddress::', toAddress)
        console.log('===eee')
        const toPubkey = new PublicKey(toAddress);
        const amount = 1 / 1e8;
        const tx = await transferNativeSol({
            payerPublicKey,
            toPubkey,
            amount: amount * 1e9,
            connection,
            provider,
            message: msg
        })
        if(!tx){
            return console.log('Error transaction')
        }
        console.log('toPubkey.toBase58()::', toPubkey.toBase58())
        const KEY = ['sol', tx, 'msg-key'].join('_')
        const send_content_text = JSON.stringify({
        text_content: msg, 
        hash: tx, 
        form_addr: payerPublicKey.toBase58(),
        to_addr: toPubkey.toBase58(),
        msgtype: 'text',
        })
        const content = AES.encrypt(send_content_text, KEY).toString()
        

        axios.post('/api/msg/add', {
        tx,
        content
        }).then((res) => {
        console.log(res)
        })

        return tx
        
    }, [address]);
    
    return {
        onSend,
    }
}